import React, { useCallback, useState, useEffect } from 'react';
import './App.css';
import logo from './img/bmd-logo.svg';
import vs from './img/versus.png';
import leftCharacter from './img/puppet_preview.png';
import rightCharacter from './img/gizmo_preview.png';

import { Chain, sepolia } from "@starknet-react/chains";
import { useAccount, useConnect, useDisconnect } from "@starknet-react/core";
import { StarknetConfig, starkscan } from "@starknet-react/core";
import { RpcProvider } from "starknet";

import ControllerConnector from '@cartridge/controller';
import { shortString } from "starknet";

const ETH_TOKEN_ADDRESS =
    "0x049d36570d4e46f48e99674bd3fcc84644ddd6b96f7c741b1562b82f9e004dc7";

const connector = new ControllerConnector({
    policies: [
        {
            target: ETH_TOKEN_ADDRESS,
            method: "create_game",
            description:
                "Create your own game match and invite your friends to play.",
        },
        {
            target: ETH_TOKEN_ADDRESS,
            method: "join_game",
            description:
                "Join a game match created by another player.",
        }, 
        {
            target: ETH_TOKEN_ADDRESS,
            method: "throw",
            description:
                "Make a throw in the game.",
        }
    ],
    rpc: "https://api.cartridge.gg/x/starknet/sepolia",
    theme: "dope-wars",
    colorMode: "light"
});

function provider(chain) {
    return new RpcProvider({
        nodeUrl: "https://api.cartridge.gg/x/starknet/sepolia",
    });
}


function App() {
    return (
        <StarknetConfig
            autoConnect
            chains={[sepolia]}
            connectors={[connector]}
            explorer={starkscan}
            provider={provider}
        > 
            <GameComponent />
        </StarknetConfig>
    );
}

function GameComponent() {
    const { connect, connectors } = useConnect();
    const { disconnect } = useDisconnect();
    const { address } = useAccount();
    const [username, setUsername] = useState();

    const [loading, setLoading] = useState(true);
    const [fadeOut, setFadeOut] = useState(false);

    const handleStartGame = () => {
        if (address) {
            alert(`Your wallet address: ${address}`);
        } else {
            alert('Please connect your wallet to start the game.');
        }
    };


    useEffect(() => {
        const timer = setTimeout(() => {
            setFadeOut(true);
            setTimeout(() => setLoading(false), 1000); // ,    
        }, 2000); //    2 
        return () => clearTimeout(timer);
    }, []);

    if (loading) {
        return (
            <div className={`App-preloader ${fadeOut ? 'fade-out' : ''}`} style={{ opacity: fadeOut ? 0 : 1 }}>
                <img src={logo} className="App-logo" alt="" />
            </div>
        );
    }

    return (
        <div className="game-screen" style={{ opacity: fadeOut ? 1 : 0 }}>
                <div className="character left-character">
                    <div className="character-name">Puppet</div>
                <img src={leftCharacter} className="App-left-character" alt="Left Character" />
            </div>
            <div className="vs-container">
                <img src={vs} className="App-vs" alt="VS" />
                <div className="play-button-container">
                    <button className="start-game-button-text" onClick={handleStartGame} disabled={!address}>
                        START GAME
                    </button>
                    <button className="play-button" onClick={() => {
                        if (address) {
                            disconnect();
                        } else {
                            connect({ connector: connectors[0] });
                        }
                    }}>
                        {address ? "DISCONNECT" : "CONNECT"}
                    </button>
                </div>
            </div>
            <div className="character right-character">
                <div className="character-name">Gizmo</div>
                <img src={rightCharacter} className="App-right-character" alt="Right Character" />
            </div>
        </div>  
    );
}

export default App;
